<template>
  <div>
    <audio id="myAudio" ref="audio" :src="musicUrl" autoplay="autoplay" muted loop></audio>
    <div class="adios">
      <div class="playbar">
        <div class="updn">
          <div class="left fl">
            <a href="javascript:void(0)" class="btn"></a>
          </div>
          <div class="right fl"></div>
        </div>
        <div class="bg"></div>
        <div class="hand"></div>
        <div class="g_plarer">
          <div class="btns fl">
            <a href="javascript:void(0)" class="prv"></a>
            <a href="javascript:void(0)" class="ply" @click="musiccl" :class="{pause: active}"></a>
            <a href="javascript:void(0)" class="nxt" @click="next"></a>
          </div>
          <div class="head fl">
            <img :src="musicImg" alt />
            <a href="javascript:void(0)" class="mask"></a>
          </div>
          <div class="play fl">
            <div class="words">
              <a href="javascript:void(0)" class="name">{{musicName}}</a>
              <span class="by">
                <span>
                  <a href="javascript:void(0)">{{musicarname}}</a>

                  <a href="javascript:void(0)"></a>
                </span>
              </span>
            </div>
            <div class="pbar">
              <div class="barbg">
                <div class="rdy"></div>
                <div class="cur" :style="{width: progress + '%'}">
                  <span class="alpha">
                    <i></i>
                  </span>
                </div>
              </div>
              <span class="time">
                <em>0{{fen}}:{{miao}}{{miaos}}</em>
                / {{time}}
              </span>
            </div>
          </div>
          <div class="oper fl">
            <a href="javascript:void(0)" class="icn-add"></a>
            <a href="javascript:void(0)" class="icn-share"></a>
          </div>
          <div class="ctrl fl">
            <div></div>
            <a href="javascript:void(0)" class="icn icn-vol" @click="mutedvol"></a>
            <a href="javascript:void(0)" class="icn icn-loop"></a>
            <span class="add">
              <span class="tip" style="display:none">已添加到播放列表</span>
              <a href="javascript:void(0)" @click="songlist" class="icn icn-list">{{listData.length}}</a>
            </span>
            <div class="list" v-show="list">
              <div class="listhd">
                <div class="listhdc">
                  <h4>播放列表（{{listData.length}}）</h4>
                  <a href="javascript:void(0)" class="addall">
                    <span class="icn-add"></span>
                    收藏全部
                  </a>
                  <span class="line"></span>
                  <a href="javascript:void(0)" class="clear">
                    <span class="icn-del"></span>
                    清除
                  </a>
                  <p class="lytit"></p>
                  <span class="close" @click="close">关闭</span>
                </div>
              </div>
              <div class="listbd">
                <div class="listbdc">
                 <scroller>
                    <ul>
                    <li :class="{bpg: item.t}" v-for="(item,index) in listData" :key="index" @mouseout="down(item,index)" @mouseover="over(item,index)">
                      <div class="col col1">
                        <div class="playicn" v-show="item.st"></div>
                      </div>
                      <div class="col col2" @click="plays(item.id,item,index)">{{item.name}}</div>
                      <div class="col col3">
                        <div class="col-show">
                          <span class="col-spa"></span>
                        </div>
                      </div>
                      <div class="col col4">
                        <span title=""><a href="javascript:void(0)">{{item.ar[0].name}}</a></span>
                      </div>
                      <div class="col col5">{{new Date(item.dt).toTimeString().substr(3,5)}}</div>
                      <div class="col col6">
                        <a href="javascript:void(0)" class="ico-src"></a>
                      </div>
                    </li>
                  </ul>
                 </scroller>
                </div>
                <div class="bline">
                  <span class="scrol" style="height: 23.4397px; display: block; top: 26px;"></span>
                </div>
                <div class="ask"><a href="" class="icn-ask"></a></div>
                <div>
                  
                    <div class="lyclist">
                      <scroller>
                      <p class="j-flag" v-for="(item,index) in songlrc" :key="index">{{item}}</p>
                      </scroller>
                    </div>
                  
                </div>
                <div class="bline-1">
                    <span class="scrol-1" style="height: 32.9514px; display: block; top: 72px;"></span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/aidos.less";
import { mapState, mapActions } from "vuex";
export default {
  props: {
    inputSong: String,
    required: true,
    
  },
  data() {
    return {
      active: false,
      list: false,
      bga: false,
      musicUrl: "",
      musicImg: "",
      musicName: "",
      musicarname: "",
      time: "",
      timer: null,
      daoji: null,
      geci: null,
      blocks: null,
      fen:0,
      miao:0,
      miaos:0,
      progress: 0,
      tableData: [],
      listData: [],
      SrcData: [],
      // 歌词
      lineArr:[],
      son:[],
      songlrc:[],
      //歌曲播放图标切换
      pause: "iconfont icon-play",
      playing: "music__list__item",
      //歌曲名
      musicName: "...",
      //作者
      artist: "...",
      //指针状态
      play: "",
      //歌曲在数组中的位置
      musicIndex: 0,
      totalTime: "",
    };
  },
  watch: {

  },
  created() {},
  computed: {
    ...mapState({
      print: (state) => state.print.all,
    }),
    ...mapState({
      print: (state) => state.print.data,
    }),
    
  },
  methods: {
    musiccl() {
      this.active = !this.active;
      // this.musicUrl = this.print.url;
      // this.tableData = this.print.table;
      // this.musicImg = this.tableData.al.picUrl;
      // this.musicName = this.tableData.name;
      // this.time = new Date(this.tableData.dt).toTimeString().substr(3, 5);
      // this.totalTime = this.time.slice(3, 5) * 1 + this.time.slice(1, 2) * 60;
      // this.musicarname = this.tableData.ar[0].name;
      if(this.active == true){
        this.active = true;
        this.daoji = setInterval(()=>{
              if(this.miaos >= 9){
                  this.miaos = 0
                  this.miao += 1
                  if(this.miao >= 6){
                    this.miao = 0
                    this.fen += 1
                    if(this.fen == this.time.slice(1, 2) && this.miaos == this.time.slice(3, 4) && this.miao == this.time.slice(4, 5)){
                      clearInterval(this.daoji);
                    }
                  }
              }else{
                this.miaos ++
              }
            },1000)
            this.timer = setInterval(() => {
                if (this.progress < 99) {
                  this.progress++;
                } else {
                  this.miaos = 0
                  this.miao = 0
                  this.fen = 0
                  this.progress = 0;
                  clearInterval(this.timer);
                  clearInterval(this.daoji);
                  this.active = false
                }
            }, (this.totalTime / 100) * 1000);
        this.$refs.audio.play();
      }else{
        clearInterval(this.timer)
        clearInterval(this.daoji)
        this.timer = null;
        this.$refs.audio.pause();
      }
    },

    //歌曲切换
    next(){

      console.log(this.SrcData);
      this.SrcData ++
    },
    songlist(){
      this.list = !this.list;
      this.listData = this.print.datas;
    },
    close(){
      this.list = false
    },
    plays(id,item,index){
      for(var i=0;i<this.listData.length;i++){
        this.listData[index].st = 1;
        this.listData[i].st = 0;
      }
      this.active = true;
      this.timer = null;
      this.daoji = null;
      this.axios({
        method:'GET',
        url:'/song/url',
        params:{
          id:id
        }
      }).then(res => {
        if(res.status == 200){
          this.SrcData = index;
          this.musicUrl = res.data.data[0].url;
          this.musicImg = item.al.picUrl;
          this.time = new Date(item.dt).toTimeString().substr(3, 5);
          this.totalTime = this.time.slice(3, 5) * 1 + this.time.slice(1, 2) * 60;
          this.musicName = item.name;
          this.musicarname = item.ar[0].name;
          if (this.active == true) {
            this.daoji = setInterval(()=>{
              if(this.miaos >= 9){
                  this.miaos = 0
                  this.miao += 1
                  if(this.miao >= 6){
                    this.miao = 0
                    this.fen += 1
                    if(this.fen == this.time.slice(1, 2) && this.miaos == this.time.slice(3, 4) && this.miao == this.time.slice(4, 5)){
                      clearInterval(this.daoji);
                    }
                  }
              }else{
                this.miaos ++
              }
            },1000)
            this.timer = setInterval(() => {
                if (this.progress < 99) {
                  this.progress++;
                } else {
                  this.miaos = 0
                  this.miao = 0
                  this.fen = 0
                  this.progress = 0;
                  clearInterval(this.timer);
                  clearInterval(this.daoji);
                  this.active = false
                }
            }, (this.totalTime / 100) * 1000);
            this.$refs.audio.play();
            
          }else{
            clearInterval(this.timer)
            clearInterval(this.daoji)
            this.timer = null;
            this.$refs.audio.pause();
            
          }
        }
      })
      this.axios({
        method:'GET',
        url:'/lyric',
        params:{
          id:id
        }
      }).then(res => {
        const regNewLine = /\n/;
        if(res.status == 200){
          this.lineArr = res.data.lrc.lyric.split(regNewLine);
          if(this.songlrc != '') return
          this.lineArr.forEach(item => {
            if (item === '') return
            
              this.songlrc.push(item.split(']')[1].trim() === '' ? '' : item.split(']')[1].trim());
              // this.songlrc = this.son; 
          });
        }
      })
      var lyclist = document.getElementsByClassName('lyclist')[0];
    },
    mutedvol(){
      document.getElementById("myAudio").volume = 0.5;

    },
    down(item){
      item.t = 0;
    },
    over(item){
      item.t = 1;
    },

  },  
};
</script>